const CreateAccontOrLogin = ({ thisIsUser, thisIsNewUser, thisNewUserName }) => {
    return (
        <>
            <div>
                {thisIsUser ?
                    (thisIsNewUser ?
                        <>
                            <p className='mx-2 py-4'><b>    !</b></p>
                            <p>     {thisNewUserName.email || thisNewUserName.phone_number}   ,        {thisNewUserName.email || thisNewUserName.phone_number}  .</p>
                        </>
                        :
                        <p className='mx-2 py-4'><b>    !</b></p>
                    )
                    :
                    (
                        <>
                            <p className='my-3'><b></b>&nbsp;<span>|</span>&nbsp;<b> </b></p>
                            <p className='d-block my-1'> !</p>
                            <p>        </p>
                        </>
                    )
                }
            </div>
        </>
    );
}
export default CreateAccontOrLogin;